.details {
  width: 50%;
  height: 100%;
  padding: 10px;
  background-color: #df095e;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.image {
  width: 80%;
  height: 200px;
  margin: 10px auto;
  background-color: #585858;
  border: 15px solid #dedede;
  border-radius: 5px;
  color: #585858;
}

.wrapper {
  width: 80%;
  height: 110px;
  padding: 10px;
  margin: 0 auto;
  background-color: #585858;
  border-radius: 5px;
  box-shadow: inset 0 0 20px rgba(0, 0, 0, 0.22);
  color: #fff;
}

.name {
  margin: 0 0 5px;
}

.char {
  font-size: 15px;
}

@media (max-width: 768px) {
  .image {
    border: 8px solid #dedede;
    height: 150px;
  }
  .name {
    font-size: 15px
  }
  .wrapper{
    height: 55px;
  }
  .char {
    font-size: 10px;
  }
}
